import { Builder } from '../Builder'

export type InputFile = InputPaidMediaPhoto|InputPaidMediaVideo

export interface InputPaidMediaPhotoPayload {
    type: 'photo'
    media: string
}

export interface InputPaidMediaVideoPayload {
    type: 'video'
    media: string
    thumbnail?: InputFile | string
    width?: number
    height?: number
    duration?: number
    supports_streaming?: boolean
}

export class InputPaidMediaPhoto extends Builder<InputPaidMediaPhotoPayload> {

    constructor() {
        super({ type: 'photo', media: '' })
    }

    public media(value: string): this {
        this.packet.media = value
        return this
    }
    
}

export class InputPaidMediaVideo extends Builder<InputPaidMediaVideoPayload> {

    constructor() {
        super({ type: 'video', media: '' })
    }

    public media(value: string): this {
        this.packet.media = value
        return this
    }

    public thumbnail(value?: InputFile | string): this {
        this.packet.thumbnail = value
        return this
    }

    public width(value?: number): this {
        this.packet.width = value
        return this
    }

    public height(value?: number): this {
        this.packet.height = value
        return this
    }

    public duration(value?: number): this {
        this.packet.duration = value
        return this
    }

    public supportsStreaming(value?: boolean): this {
        this.packet.supports_streaming = value
        return this
    }

}